@charset "UTF-8";
/* Data panel */
/* To change and add to the data-panel CSS component */
.data-panel-header {
  cursor: pointer;
  padding: 1.5rem;
  margin-bottom: 0;
  color: #4263c2;
}
.data-panel-header:hover {
  text-decoration: underline;
  background-color: #eeecec;
}
.data-panel-header::after {
  content: "➕";
  /* Unicode character for "plus" sign (+) */
  color: #4263c2;
  float: right;
  margin-left: 5px;
}
.data-panel-header::after.active::after {
  content: "➖";
  /* Unicode character for "minus" sign (-) */
}
.data-panel-header__title {
  border: 0;
  background: none;
  color: #4263c2;
  padding: 0;
  display: none;
}

.data-panel-body {
  padding: 1.5rem 1.5rem 0.5rem 1.5rem;
}

/* Styles for non-js file-picker */
.upload-file::before {
  content: " ";
  background-size: 1.6rem;
  min-width: 1.5rem;
  height: 1.5rem;
  margin-left: 0.5em;
  float: right;
  font-size: 0.8rem;
}

input[type=file]::-webkit-file-upload-button {
  font-size: 1.25rem;
  font-size: 1rem;
  border: none;
  border-right: 2px solid #033e58;
  padding: 0.5rem 1rem;
  background: #f5f6ff;
  color: #033e58;
}

input[type=file] {
  border: 2px solid #033e58;
  border-radius: 4px;
  width: 100%;
  display: block;
  margin-bottom: 0;
  background: white;
}

input[type=file]:hover,
input[type=file]::-webkit-file-upload-button:hover {
  cursor: pointer;
}

/* Styles for the footer */
footer {
  background-color: #eeecec;
  margin-top: 3rem;
}
footer ul {
  list-style: none;
  margin: 0 auto;
}
footer ul li {
  display: inline-block;
  margin-right: 2rem;
}

.footer-content {
  max-width: 1248px;
  margin: 0 auto;
  padding: 2rem 0;
  text-align: center;
}

/* old footer styles */
.page__footer {
  display: none;
}

#footer ul {
  padding: 0;
}
#footer ul li {
  display: inline-block;
  margin: 0 16px 0 0;
}
#footer a {
  color: #454a4c;
}
#footer a:visited {
  color: #4c2c92;
}

h1.u-fs-xl {
  font-weight: 500;
}

h2.u-fs-l {
  font-weight: 500;
}

h2.u-fs-r--b {
