  content: " ●";
  font-size: 16px;
  color: #4263c2;
  margin-left: -0.75rem;
  margin-right: 0.2rem;
}

/* Create new message */
input.secure-message--recipient {
  font-size: 1rem;
  margin-bottom: 1rem;
}

input.secure-message--subject {
  border: 1px solid #222;
  margin-bottom: 1rem;
}

label.secure-message--subject {
  margin-top: 1rem;
}

textarea.secure-message--message {
  border: 1px solid #222;
  margin-bottom: 1rem;
}

.sm-form-recipient-label,
.sm-form-meta-label {
  display: inline-block;
  width: 5rem;
  color: #666;
}

.secure-message-conversation-meta__label {
  display: inline-block;
  width: 5rem;
  color: #666;
}

.secure-message--subject,
.secure-message--message {
  display: block;
  width: 100%;
}

@media only screen and (min-width: 740px) {
  .create-message.block {
    width: 60%;
