}
/* Message list table */
.message-list {
  margin-top: 1.5rem;
  margin-bottom: 1rem;
  width: 100%;
}
.message-list thead tr,
.message-list tbody tr {
  border-bottom: 2px solid #666;
}
.message-list tbody tr {
  border-width: 1px;
}
.message-list tbody tr:hover {
  cursor: pointer;
  background: #e4d8eb;
  background: #eee;
}
.message-list th,
.message-list td {
  text-align: left;
  padding: 0.5rem 0 0.5rem 1rem;
}
.message-list th {
  padding-bottom: 0.25rem;
  background: white;
}
.message-list__date-time, .message-list__ru-ref, .message-list__ru-name, .message-list__subject, .message-list__from, .message-list__to {
  white-space: nowrap;
}
.message-list__date-time {
  width: 10rem;
}
.message-list__ru-ref {
  width: 8rem;
}
.message-list__ru-name {
  max-width: 16rem;
  overflow: hidden;
  text-overflow: ellipsis;
}
.message-list__subject {
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.message-list__from {
  width: 10rem;
  overflow: hidden;
  text-overflow: ellipsis;
}
.message-list__to {
  padding: 0.5rem 1rem 0.5rem 1rem;
  width: 8rem;
  overflow: hidden;
  text-overflow: ellipsis;
}
.message-list__item--unread {
  font-weight: 800;
}

@media only screen and (min-width: 740px) {
  .message-list__date-time,
.message-list__ru-ref,
.message-list__ru-name,
.message-list__subject,
.message-list__from,
.message-list__to {
    padding-right: 2rem;
  }

  .message-list__subject {
    min-width: 250px;
  }

  .message-list__from {
    min-width: 150px;
  }

  .message-list__to {
    min-width: 150px;
  }
}
/* Conversation views */
@media only screen and (min-width: 740px) {
  .secure-message-conversation {
