/* Tabbed navigation */
.nav-tabs {
  list-style: none;
  padding: 0;
  border-bottom: 1px solid #666;
}
.nav-tabs__tab, .nav-tabs__tab--active {
  display: inline-block;
  list-style: none;
  padding: 0;
  margin: 0;
  border: none;
}

.tab-label {
  display: inline-block;
  padding: 0.3rem 1rem;
  margin: 0;
  margin-bottom: -2px;
  border: 1px solid white;
  border-top-left-radius: 0.2rem;
  border-top-right-radius: 0.2rem;
  border-bottom: 0;
  color: #222;
  font-weight: 400;
}

.nav-tabs__tab--active .tab-label {
  font-weight: 600;
  border: 1px solid #666;
  background: white;
  border-bottom: 1px solid white;
}

.nav-tabs__tab .tab-label {
  text-decoration: none;
}

.nav-tabs__tab .tab-label:hover,
.nav-tabs__tab .tab-label:focus {
  text-decoration: underline;
  border: 1px solid #666;
  background-color: #4263c2;
}

.nav-tabs__tab .tab-label:hover {
  text-decoration: underline;
  background-color: #4263c2;
  color: white;
}

.nav-tabs__tab.create {
  float: right;
}

.tab-label.create-new-message {
  text-decoration: underline;
  color: #4263c2;
}

.tab-label.create-new-message:active,
.tab-label.create-new-message:focus {
  background-color: #4263c2;
  color: white;
}

.tab-label.create-new-message:hover {
  text-decoration: underline;
  background-color: #4263c2;
  color: white;
}

.pagination {
  color: black;
  padding: 1px;
  display: inline;
}
.pagination ul {
  color: black;
  float: left;
  padding-left: 0;
  display: inline;
}
.pagination li {
  list-style-type: none;
  float: left;
  padding-top: 0.4rem;
  padding-bottom: 0.4rem;
  border-radius: 0.2rem;
  display: inline;
}
.pagination li a {
  padding: 8px 16px;
}
.pagination li.active {
  margin: 0 6px;
  background-color: #033e58;
}
.pagination li.active a {
  color: white;
}
.pagination li.disabled a {
  color: #666;
  text-decoration: none;
}
.pagination li:hover:not(.active) {
  background-color: #e4e8eb;
  transition: background-color 0.3s;
}
.pagination li.previous.disabled.unavailable {
  display: none;
}
.pagination li.next.disabled {
  display: none;
}

